import React, { useCallback, useEffect, useState } from "react";
import style from "./detailLeft.module.less";
import { Link } from "react-router-dom";
import { getLyric } from "../../../services/apis";
import { Button, Space, Toast, Pagination, Spin } from "@douyinfe/semi-ui";
import {
	IconPlayCircle,
	IconPlus,
	IconForward,
	IconDownload,
	IconComment,
	IconChevronUp,
	IconChevronDown,
} from "@douyinfe/semi-icons";
import { getSongComment, getSongUrl, getMusicPlayUrl } from "../../../services/apis";
import CommitList from "../../../components/CommitList/CommitList";
import Commit from "../../../components/Commit";
import { comment, likeComment } from "../../../services/comment";
import { addSongAction } from "../../../store/actions/song";
import { setCurSongIdAction } from "@/store/actions/curSongId";
import { connect } from "react-redux";
import utils from "../../../utils";

const mapStateToProps = (state) => {
	return {
		song: state.song
	}
};

const mapDispatchToProps = (dispatch) => {
	return {
		addSong: (...args) => dispatch(addSongAction(...args)),
		setCurSongId: (...args) => dispatch(setCurSongIdAction(...args))
	};
};

function DetailLeft(props) {
	const { id, addSong, setCurSongId } = props; // æ­Œæ›²ID
	const [lyric, setLyric] = useState(""); // æ­Œè¯
	const [open, setOpen] = useState(false); // æ˜¯å¦å±•å¼€æ­Œè¯
	const [comments, setComments] = useState([]); // è¯„è®ºåˆ—è¡¨
	const [hotComments, setHotComments] = useState([]); // çƒ­é—¨è¯„è®ºåˆ—è¡¨
	const [page, setPage] = useState(1); // è¯„è®ºåˆ—è¡¨
	const [limit, setLimit] = useState(20); // é¡µå®¹é‡
	const [total, setTotal] = useState(0); // æ€»è¯„è®ºæ•°
	const [loading, setLoading] = useState(false); // loading
	const detail = props.detail;
	// è·å–æ­Œè¯
	useEffect(() => {
		(async () => {
			const res = await getLyric({
				id
			});
			if (res.code === 200) {
				setLyric(res.lrc.lyric.replace(/\[.*?\]|\x20/g, ''));
			}
		})();
		return () => {
		}
	}, [id])
	// æäº¤è¯„è®º
	const songCommit = useCallback(async (val) => {
		const res = await comment({
			t: 1,
			type: 0,
			id,
			content: val,
			timestamp: Date.now(),
		});
		if (res.code === 200) {
			Toast.success({
				content: "è¯„è®ºæˆåŠŸ",
				duration: 2,
			});
			getComments();
		}
	}, []);
	// é¡µç å˜åŒ–
	const handlePageChange = useCallback((page) => {
		setPage(page);
	}, []);
	// é¡µå®¹é‡å˜åŒ–
	const handleLimitChange = useCallback((limit) => {
		setLimit(limit);
	}, []);
	// è·å–è¯„è®º
	const getComments = async () => {
		setLoading(true)
		const res = await getSongComment({
			id,
			limit,
			offset: (page - 1) * limit,
			timestamp: Date.now(),
		});
		if (res.code === 200) {
			setLoading(false);
			setTotal(res.total);
			setComments(res.comments);
			setHotComments(res.hotComments);
		}
	}
	// è·å–è¯„è®ºåˆ—è¡¨
	useEffect(() => {
		getComments();
		return () => { };
	}, [id, limit, page]);

	// å¯¹è¯„è®º ç‚¹èµ/å–æ¶ˆ
	const like = async (cid, liked) => {
		const res = await likeComment({
			id,
			cid,
			t: liked ? 0 : 1,
			type: 0,
			timestamp: Date.now(),
		});
		if (res.code === 200) {
			if (liked) {
				Toast.success({
					content: "å–æ¶ˆèµæˆåŠŸ",
					duration: 2,
				})
			} else {
				Toast.success({
					content: "èµæˆåŠŸ",
					duration: 2,
				})
			}
			getComments();
		} else {
			Toast.error({
				content: "æ“ä½œå¤±è´¥",
			})
		}
	};

	// åˆ é™¤è¯„è®º
	const del = async (cid) => {
		const res = await comment({
			t: 0,
			type: 0,
			id: id,
			commentId: cid,
			timestamp: Date.now(),
		});
		if (res.code === 200) {
			Toast.success({
				content: "åˆ é™¤æˆåŠŸ",
				duration: 2,
			});
			getComments();
		}
	};

	// æ’­æ”¾æ­Œæ›²
	const play = async () => {
		const res = await getMusicPlayUrl({ id });
		setCurSongId(id);
		if (res.code === 200 && res.data[0].url) {
			addSong(res.data);
		} else {
			Toast.error({
				content: "æ— æƒé™",
				duration: 2
			})
		}
	};

	// ä¸‹è½½æ­Œæ›²
	const download = async () => {
		const res = await getSongUrl({ id });
		console.log(res);
	};
	return (
		<div className={style["detail-left"]}>
			<div className="top-content">
				<div className="img">
					<img src={detail.al.picUrl} alt="" />
					<span className="img-mask"></span>
				</div>
				<div className="list-content">
					<div className="list-name">
						<i className="icon-song"></i>
						{detail.name}
					</div>
					<div className="sub-name">
						{detail.alia.map((item, index) => <span key={index} className="sub-item">{item}</span>)}
					</div>
					<div className="row">
						<span className="label">æ­Œæ‰‹ï¼š</span>
						<div className="wrap">
							{detail.ar.map((a, i) => (i === 0 ? <Link key={a.id} className="item" to={'/find/artist?id=' + a.id}>{a.name}</Link> :
								<span key={a.id} className="divider">/<Link className="item" to={'/find/artist?id=' + a.id}>{a.name}</Link></span>
							))}
						</div>
					</div>
					<div className="row">
						<span className="label">æ‰€å±ä¸“è¾‘ï¼š</span>
						<div className="wrap">
							<Link className="item" to={'/find/album?id=' + detail.al.id}>{detail.al.name}</Link>
						</div>
					</div>
					<div className="operates">
						<Space>
							<Button onClick={play} type="danger" icon={<IconPlayCircle />}>æ’­æ”¾</Button>
							<Button type="tertiary" icon={<IconPlus />}>æ”¶è—</Button>
							<Button type="tertiary" icon={<IconForward />}>åˆ†äº«</Button>
							<Button onClick={download} type="tertiary" icon={<IconDownload />}>ä¸‹è½½</Button>
							<Button type="tertiary" icon={<IconComment />} onClick={() => {
								utils.goAnchor("#comment");
							}}>
								({total})
							</Button>
						</Space>
					</div>
					<div className="lyric">{open ? lyric : lyric.split("\n").slice(0, 16).join("\n")}</div>
					<div className="arrow" onClick={() => {
						setOpen(!open);
					}}>
						<span className="text">{open ? 'æ”¶èµ·' : 'å±•å¼€'}</span>
						{open ? <IconChevronUp /> : <IconChevronDown />}
					</div>
				</div>
			</div>
			<Commit
				commitNum={total}
				commit={songCommit}
				commitLength={140}
			/>
			<CommitList like={like} del={del} total={total} comments={comments} hotComments={hotComments} />
			{total > 0 ? (
				<div className="pagination-wrapper">
					<Pagination
						total={total}
						currentPage={page}
						onPageChange={handlePageChange}
						showSizeChanger
						pageSize={limit}
						pageSizeOpts={[20, 30, 50]}
						onPageSizeChange={handleLimitChange}
					></Pagination>
				</div>
			) : null}
			<Spin
				spinning={loading}
				tip="loading..."
				size="large"
				style={{
					position: "fixed",
					top: "50%",
					left: "50%",
					transform: "translate(-50%, -50%)",
					zIndex: "9999",
				}}
			></Spin>
		</div>
	);
}

export default connect(mapStateToProps, mapDispatchToProps)(DetailLeft);
