import React, { useEffect, useState } from "react";
import style from "./index.module.less";
import { useLocation } from "react-router-dom";
import qs from "query-string";
import { getSongDetail } from "../../../services/apis";
import DetailLeft from "./DetailLeft";
import DetailRight from "./DetailRight";
import {
  IllustrationConstruction,
  IllustrationConstructionDark,
} from "@douyinfe/semi-illustrations";
import { Empty } from "@douyinfe/semi-ui";

export default function SongDetail() {
  const [songDetail, setSongDetail] = useState(null); // æ­Œæ›²è¯¦æƒ…
  const location = useLocation();
  const ids = qs.parse(location.search).id; // æ­Œæ›²id
  // è·å–æ­Œæ›²è¯¦æƒ…
  useEffect(() => {
    (async () => {
      const res = await getSongDetail({
        ids,
      });
      if (res.code === 200) {
        setSongDetail(res.songs);
      }
    })();
    return () => {};
  }, [ids]);
  return (
    <>
      {songDetail ? (
        <div className={style["song-detail"]}>
          <div className="left">
            <DetailLeft detail={songDetail[0]} id={ids} />
          </div>
          <div className="right">
            <DetailRight id={ids} />
          </div>
        </div>
      ) : (
        <Empty
          image={
            <IllustrationConstruction style={{ width: 150, height: 150 }} />
          }
          darkModeImage={
            <IllustrationConstructionDark style={{ width: 150, height: 150 }} />
          }
          description={"ä¿¡æ¯ä¸¢å¤±"}
          style={{ padding: 30 }}
        />
      )}
    </>
  );
}
