export async function getCommentList({id, type, pageNo = 1, pageSize = 20, timestamp}) {
    const res = await ins.get("/comment/new", {
        params: {
            id,
            type, 
            pageNo, 
            pageSize,
            timestamp
        }
    });
    return res;
};

